import React, { Component } from 'react';
import { Field, reduxForm } from 'redux-form';
import { connect } from 'react-redux';
import { sectors } from '../../helpers/util';
import { Redirect } from 'react-router-dom';
import axios from 'axios';
import {
  Grid,
  Button,
  Paper,
  Avatar,
  TextField,
  MenuItem,
} from '@material-ui/core/';

import { RenderTextInput, RenderTextSelect, Navbar, Flash } from '..';
import { adminSignupValidate } from '../../helpers/validate';
import { adminUpdate, adminUnmount, getAdmin } from '../../actions/admin';

import '../../stylesheets/styles.css';
import { objForm } from '../../helpers/util';
const src =
  'data:image/svg+xml;base64,';

class AdminUpdate extends Component {
  constructor() {
    super();
    this.state = {
      admin: {},
    };
  }
  handleSubmit = (values) => {
    const { dispatch } = this.props;
    dispatch(adminUpdate(this.state.admin));
    //dispatch(adminSignup(values));
  };
  handleChange = (event, newValue, previousValue, name) => {
    const { admin } = this.state;
    admin[name] = newValue;
    this.setState({ admin });
  };
  componentDidMount = async () => {
    const admin = await axios.get('/api/currentAdmin');
    this.props.dispatch(getAdmin(admin.data));
    this.setState({ admin: admin.data });
  };
  componentWillUnmount = async () => {
    this.props.dispatch(adminUnmount());
  };
  render() {
    const { handleSubmit, submitting } = this.props;
    const { admin } = this.state;
    const { isAdminUpdated } = this.props.authAdmin;

    if (isAdminUpdated) {
      return <Redirect to="/admin/info" />;
    }
    return (
      <div>
        <Navbar />
        <Flash />
        <div className="my-4 paperStyle row justify-content-center">
          <div className="col-xl-5 col-lg-6 col-md-7 col-sm-8 col-10">
            <div>
              {/* {isLoggedIn && <div>Successfully Logged In</div>} */}
              <Grid>
                <Paper elevation={10} style={{ borderRadius: '5%' }}>
                  <form
                    method="Post"
                    onSubmit={handleSubmit(this.handleSubmit)}
                    style={{ padding: '1rem' }}
                    className="px-3"
                  >
                    <Grid align="center">
                      <Avatar src={src} alt="Lock-img">
                        {' '}
                      </Avatar>
                      <h1> Edit Your Details </h1>
                    </Grid>
                    <Field
                      name="storeName"
                      label="Store Name"
                      type="text"
                      Value={admin.storeName}
                      value={admin.storeName}
                      onChange={this.handleChange}
                      component={RenderTextInput}
                    />
                    <Field
                      name="category"
                      label="Category"
                      component={RenderTextSelect}
                      onChange={this.handleChange}
                    >
                      {sectors.map((sector) => (
                        <MenuItem key={sector} value={sector}>
                          {sector}
                        </MenuItem>
                      ))}
                    </Field>
                    <Field
                      name="pinCode"
                      label="Pincode"
                      type="number"
                      Value={admin.pinCode}
                      value={admin.pinCode}
                      onChange={this.handleChange}
                      component={RenderTextInput}
                    />
                    <Field
                      name="address"
                      label="Address"
                      type="text"
                      Value={admin.address}
                      value={admin.address}
                      onChange={this.handleChange}
                      component={RenderTextInput}
                    />
                    <Field
                      name="city"
                      label="City"
                      type="text"
                      Value={admin.city}
                      value={admin.city}
                      onChange={this.handleChange}
                      component={RenderTextInput}
                    />
                    <Field
                      name="state"
                      label="State"
                      type="text"
                      Value={admin.state}
                      value={admin.state}
                      onChange={this.handleChange}
                      component={RenderTextInput}
                    />
                    <Field
                      name="country"
                      label="Country"
                      type="text"
                      Value={admin.country}
                      value={admin.country}
                      onChange={this.handleChange}
                      component={RenderTextInput}
                    />
                    <Field
                      name="gstIn"
                      label="GST registration number"
                      type="text"
                      Value={admin.gstIn}
                      value={admin.gstIn}
                      onChange={this.handleChange}
                      component={RenderTextInput}
                    />
                    {/*<button type="submit" disabled={submitting}>
                    Submit
                  </button>*/}

                    <Button
                      style={{
                        marginBottom: '1rem',
                        marginTop: '1rem',
                        color: '#23DCBB',
                      }}
                      type="submit"
                      variant="contained"
                      color="secondary"
                      disabled={submitting}
                      fullWidth
                    >
                      Edit Details
                    </Button>
                  </form>
                </Paper>
              </Grid>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
const AdminUpdateForm = reduxForm({
  form: 'adminSignup', // a unique identifier for this form
  validate: adminSignupValidate,
  initialValues: {},
})(AdminUpdate);
const mapStatetoProps = ({ authAdmin, authUser }) => {
  return { authAdmin, authUser, initialValues: { ...authAdmin.admin } };
};
export default connect(mapStatetoProps)(AdminUpdateForm);
