import React, { Component } from 'react';
import { Redirect } from 'react-router-dom';
import { Field, reduxForm } from 'redux-form';
import {
  Grid,
  Button,
  Paper,
  Avatar,
  TextField,
  FormControlLabel,
  Checkbox,
  Link,
  Box,
} from '@material-ui/core/';

import { RenderTextInput } from '../';
import { loginValidate } from '../../helpers/validate';
import { login } from '../../actions/user';
import { connect } from 'react-redux';

import '../../stylesheets/styles.css';

const src =
  'data:image/svg+xml;base64,';

class Login extends Component {
  constructor() {
    super();
  }

  handleSubmit = async (values) => {
    const { dispatch } = this.props;
    dispatch(login(values));
  };
  render() {
    const { handleSubmit, submitting } = this.props;
    const { isLoggedIn } = this.props.authUser;
    const { from } = this.props.location.state || {
      from: { pathname: '/products' },
    };
    if (isLoggedIn) {
      return <Redirect to={from} />;
    }
    return (
      <div>
        <div>
          <Grid>
            <Paper elevation={10} className="paperStyle">
              <form
                method="Post"
                onSubmit={handleSubmit(this.handleSubmit)}
                className="formStyle"
              >
                <Grid align="center">
                  <Avatar src={src} alt="Lock-img">
                    {' '}
                  </Avatar>
                  <h1> Log In </h1>
                </Grid>
                <Field
                  name="username"
                  label="Username"
                  type="text"
                  component={RenderTextInput}
                />
                <Field
                  name="password"
                  label="Password"
                  type="password"
                  component={RenderTextInput}
                />
                <FormControlLabel
                  label="Remember me"
                  control={<Checkbox name="checkedB" color="primary" />}
                />
                <Grid align="center">
                  <button
                    style={{
                      marginBottom: '1rem',
                      marginTop: '1rem',
                    }}
                    type="submit"
                    disabled={submitting}
                  >
                    Log In
                  </button>
                  <Box style={{ marginBottom: '1rem' }}>
                    <Link href=""> Forgot Password </Link>
                  </Box>
                  Don't have an account?
                  <Link href=""> Sign Up </Link>
                </Grid>
              </form>
            </Paper>
          </Grid>
        </div>
      </div>
    );
  }
}
const LoginForm = reduxForm({
  form: 'userLogin', // a unique identifier for this form
  validate: loginValidate,
})(Login);
const mapStatetoProps = ({ authUser }) => {
  return { authUser };
};
export default connect(mapStatetoProps)(LoginForm);
