import React, { Component } from 'react';
import { Field, reduxForm } from 'redux-form';
import { connect } from 'react-redux';
import { Grid, Button, Paper, Avatar, TextField } from '@material-ui/core/';
import { Redirect } from 'react-router-dom';
import { RenderTextInput } from '../';
import { signupValidate } from '../../helpers/validate';
import { signup } from '../../actions/user';

import '../../stylesheets/styles.css';

const src =
  'data:image/svg+xml;base64,';

class Signup extends Component {
  constructor() {
    super();
  }
  handleSubmit = (values) => {
    const { dispatch } = this.props;
    dispatch(signup(values));
  };
  render() {
    const { handleSubmit, submitting } = this.props;
    const { isLoggedIn } = this.props.authUser;
    if (isLoggedIn) {
      return <Redirect to="/" />;
    }
    return (
      <div>
        <div>
          <Grid>
            <Paper elevation={10} className="paperStyle">
              <form
                method="Post"
                onSubmit={handleSubmit(this.handleSubmit)}
                className="formStyle"
              >
                <Grid align="center">
                  <Avatar src={src} alt="Lock-img">
                    {' '}
                  </Avatar>
                  <h1> Sign Up </h1>
                </Grid>
                <Field
                  name="username"
                  label="Username"
                  type="text"
                  component={RenderTextInput}
                />
                <Field
                  name="firstName"
                  label="FirstName"
                  type="text"
                  component={RenderTextInput}
                />
                <Field
                  name="lastName"
                  label="LastName"
                  type="text"
                  component={RenderTextInput}
                />
                <Field
                  name="email"
                  label="Email"
                  type="email"
                  component={RenderTextInput}
                />
                <Field
                  name="password"
                  label="Password"
                  type="password"
                  component={RenderTextInput}
                />
                <Grid align="center">
                  <button
                    style={{
                      marginTop: '0.6rem',
                    }}
                    type="submit"
                    disabled={submitting}
                  >
                    Sign Up
                  </button>
                </Grid>
              </form>
            </Paper>
          </Grid>
        </div>
      </div>
    );
  }
}
const SignupForm = reduxForm({
  form: 'userSignup', // a unique identifier for this form
  validate: signupValidate,
})(Signup);
const mapStatetoProps = ({ authUser }) => {
  return { authUser };
};
export default connect(mapStatetoProps)(SignupForm);



