import React, { Component } from 'react';
import { Field, reduxForm } from 'redux-form';
import { connect } from 'react-redux';
import { sectors } from '../../helpers/util';
import { Redirect } from 'react-router-dom';
import {
  Grid,
  Button,
  Paper,
  Avatar,
  TextField,
  MenuItem,
} from '@material-ui/core/';

import { RenderTextInput, RenderTextSelect, Navbar, Flash } from '..';
import { adminSignupValidate } from '../../helpers/validate';
import { adminSignup } from '../../actions/admin';
import { ImageComponent } from '..';
import '../../stylesheets/styles.css';

const src =
  'data:image/svg+xml;base64,';

class AdminSignup extends Component {
  constructor() {
    super();
    this.state = {
      file: {},
    };
  }
  addImage = (e) => {
    this.setState({
      file: e.target.files[0],
    });
  };
  handleSubmit = (values) => {
    const { dispatch } = this.props;
    const fd = new FormData();
    fd.append('image', this.state.file);
    for (let key in values) {
      fd.append(key, values[key]);
    }
    dispatch(adminSignup(fd));
  };
  render() {
    const { handleSubmit, submitting } = this.props;
    const { isAdmin } = this.props.authUser;
    const { from } = this.props.location.state || {
      from: { pathname: '/products' },
    };
    if (isAdmin) {
      return <Redirect to={from} />;
    }
    return (
      <div>
        <Navbar />
        <Flash />
        <div className="my-4 paperStyle row justify-content-center">
          <div className="col-xl-5 col-lg-6 col-md-7 col-sm-8 col-10">
            <div>
              {/* {isLoggedIn && <div>Successfully Logged In</div>} */}
              <Grid>
                <Paper elevation={10} style={{ borderRadius: '5%' }}>
                  <form
                    method="Post"
                    onSubmit={handleSubmit(this.handleSubmit)}
                    style={{ padding: '1rem' }}
                    className="px-3"
                  >
                    <Grid align="center">
                      <Avatar src={src} alt="Lock-img">
                        {' '}
                      </Avatar>
                      <h1> Tell Us About your Bussiness </h1>
                    </Grid>
                    <Field
                      name="storeName"
                      label="Store Name"
                      type="text"
                      component={RenderTextInput}
                    />
                    <Field
                      name="category"
                      label="Category"
                      component={RenderTextSelect}
                    >
                      {sectors.map((sector) => (
                        <MenuItem key={sector} value={sector}>
                          {sector}
                        </MenuItem>
                      ))}
                    </Field>
                    <Field
                      name="pinCode"
                      label="Pincode"
                      type="number"
                      component={RenderTextInput}
                    />
                    <Field
                      name="address"
                      label="Address"
                      type="text"
                      component={RenderTextInput}
                    />
                    <Field
                      name="city"
                      label="City"
                      type="text"
                      component={RenderTextInput}
                    />
                    <Field
                      name="state"
                      label="State"
                      type="text"
                      component={RenderTextInput}
                    />
                    <Field
                      name="country"
                      label="Country"
                      type="text"
                      component={RenderTextInput}
                    />
                    <Field
                      name="gstIn"
                      label="GST registration number"
                      type="text"
                      component={RenderTextInput}
                    />
                    <Field
                      name="image"
                      type="file"
                      addImage={this.addImage}
                      multipleImages={false}
                      heading="Store Logo"
                      component={ImageComponent}
                    />
                    {/*<button type="submit" disabled={submitting}>
                    Submit
                  </button>*/}

                    <Button
                      style={{
                        marginBottom: '1rem',
                        marginTop: '1.5rem',
                        color: '#23DCBB',
                      }}
                      type="submit"
                      variant="contained"
                      color="secondary"
                      disabled={submitting}
                      fullWidth
                    >
                      Register
                    </Button>
                  </form>
                </Paper>
              </Grid>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
const AdminSignupForm = reduxForm({
  form: 'adminSignup', // a unique identifier for this form
  validate: adminSignupValidate,
})(AdminSignup);
const mapStatetoProps = ({ authAdmin, authUser }) => {
  return { authAdmin, authUser };
};
export default connect(mapStatetoProps)(AdminSignupForm);
